<template>
  <div class="nWrapper">
    <div class="socialEcoTitle">
      <div class="sociltitlehead">公告简讯</div>
      <div>
        <img src="../../assets/imgs/point1 (1).png" alt />
      </div>
    </div>
    <div class="infowrapper">
      <div class="infoTable" v-for="(v,i) in news " :key="i">
        <img class="firstImg" @click="readmoreinfo(v.newsId)" :src="v.image" alt />
        <div @click="readmoreinfo(v.newsId)" class="infoTitle">{{v.title}}</div>
        <div @click="readmoreinfo(v.newsId)" class="infoContent">{{v.newsAbstract}}</div>
        <div class="inobottom">
          <div class="infodate">{{v.createTime}}</div>
          <div @click="readmoreinfo(v.newsId)" class="readmore">查看更多</div>
        </div>
      </div>

      <div class="bottomNavNotice">
        <el-pagination
          prev-text="上页"
          next-text="下页"
          :pager-count="5"
          layout="prev, pager, next"
          :page-size="6"
          :total="totalPage"
          @prev-click="pageprev"
          @next-click="pagenext"
          @current-change="nowPage"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getNews } from "../../../api";
// import { log } from 'util';
import moment from "moment/moment";
export default {
  mounted() {
    getNews(1, 6, "desc", "id")
      .then(res => {
        window.console.log(res);

        for (let v of res.data.rows) {
          v.createTime = moment(v.createTime).format("YYYY/MM/DD");
          // console.log(v.createTime);
        }
        this.totalPage = res.data.total;
        this.news = res.data.rows;
      })
      .catch(err => {
        window.console.log(err);
      });
  },
  data() {
    return {
      news: [],
      totalPage: 100
    };
  },
  methods: {
    readmoreinfo(i) {
      this.$router.push({ path: "/Dynamic/info", query: { nu: i } });
    },
    pageprev(i) {
      getNews(i, 6, "desc", "id")
        .then(res => {
          for (let v of res.data.rows) {
            // var date = new Date(v.createTime);
            // var year = date.getFullYear();
            // var month =
            //   date.getMonth() + 1 < 10
            //     ? "0" + (date.getMonth() + 1)
            //     : date.getMonth() + 1;
            // var day =
            //   date.getDate() < 10 ? "0" + date.getDate() : date.getDate();

            // v.createTime = year + "-" + month + "-" + day;
            v.createTime = moment(v.createTime).format("YYYY/MM/DD");
          }
          this.totalPage = res.data.total;
          this.news = res.data.rows;
        })
        .catch(err => {
          window.console.log(err);
        });
      window.console.log(i);
    },
    pagenext(n) {
      getNews(n, 6, "desc", "id")
        .then(res => {
          for (let v of res.data.rows) {
            // var date = new Date(v.createTime);
            // var year = date.getFullYear();
            // var month =
            //   date.getMonth() + 1 < 10
            //     ? "0" + (date.getMonth() + 1)
            //     : date.getMonth() + 1;
            // var day =
            //   date.getDate() < 10 ? "0" + date.getDate() : date.getDate();

            // v.createTime = year + "-" + month + "-" + day;
            v.createTime = moment(v.createTime).format("YYYY/MM/DD");
          }
          this.totalPage = res.data.total;
          this.news = res.data.rows;
        })
        .catch(err => {
          window.console.log(err);
        });
      window.console.log(n);
    },
    nowPage(p) {
      getNews(p, 6, "desc", "id")
        .then(res => {
          for (let v of res.data.rows) {
            // var date = new Date(v.createTime);
            // var year = date.getFullYear();
            // var month =
            //   date.getMonth() + 1 < 10
            //     ? "0" + (date.getMonth() + 1)
            //     : date.getMonth() + 1;
            // var day =
            //   date.getDate() < 10 ? "0" + date.getDate() : date.getDate();

            // v.createTime = year + "-" + month + "-" + day;
            v.createTime = moment(v.createTime).format("YYYY/MM/DD");
          }
          this.totalPage = res.data.total;
          this.news = res.data.rows;
        })
        .catch(err => {
          window.console.log(err);
        });
    }
  }
};
</script>

<style lang="less"   >
.nWrapper {
  background: #f4f4f4;
  padding: 1.34rem 0.67rem 1.32rem 0.67rem;
  .infoTable {
    width: 3.95rem;
    margin-bottom: 0.66rem;
    .firstImg {
      width: 3.95rem;
      height: 3.82rem;
    }
    .infoTitle {
      font-size: 0.38rem;
      color: #010101;
      margin-bottom: 0.64rem;
      margin-top: 0.66rem;
      height: 1.2rem;
    }
    .infoContent {
      font-size: 0.34rem;
      color: rgba(0, 0, 0, 0.7);
      margin-bottom: 0.67rem;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 5;
      overflow: hidden;
    }
    .inobottom {
      display: flex;
      justify-content: space-between;
      font-size: 0.3rem;
      color: #6e6e6e;
      padding-bottom: 0.33rem;
      border-bottom: 0.01rem solid #6e6e6e;
    }
  }
}
.infowrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.socialEcoTitle {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 0.67rem;
  // margin-top: 1.11rem;
  border-bottom: 0.01rem solid #737373;
  margin-bottom: 0.64rem;
  .sociltitlehead {
    font-size: 0.51rem;
    color: #4c4c4c;
  }
}
.bottomNavNotice {
  display: flex;
  justify-content: center;
  margin-top: 0.71rem;
  width: 100%;

  .el-pager li {
    width: 0.71rem !important;
    height: 0.71rem !important;
    border-radius: 0.355rem !important;
    line-height: 0.71rem !important;
    font-size: 0.24rem !important;
    background: transparent !important;
    border: 1px solid #9b9b9f;
    color: #9b9b9f;
    margin: 0 0.2rem !important;
    padding: 0 !important;
    min-width: 0.71rem !important;
  }

  .el-pagination span:not([class*="suffix"]) {
    width: 0.71rem !important;
    height: 0.71rem !important;
    border-radius: 0.355rem !important;
    line-height: 0.71rem !important;
    font-size: 0.24rem !important;
    padding: 0 !important;
    background: transparent !important;
    color: #9b9b9f;
  }
  .el-pagination button {
    width: 0.71rem !important;
    height: 0.71rem !important;
    border-radius: 0.355rem !important;
    line-height: 0.71rem !important;
    font-size: 0.24rem !important;
    padding: 0 !important;
    background: transparent !important;
    border: 1px solid #9b9b9f;
    margin: 0 0.2rem;
    text-align: center;
  }
  .el-pagination span:not([class*="suffix"]):active {
    color: #fff !important;
  }
  .el-pagination button:active {
    background: #ff7000 !important;
  }
  .el-pager li.active {
    color: #fff !important;
    background: #ff7000 !important;
  }
  .el-pagination {
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 0;
    height: 0.71rem;
  }
  .el-pagination button,
  .el-pagination span:not([class*="suffix"]) {
    min-width: 0.71rem !important;
  }
}
</style>